export default {
  data() {
    return {
      list: [
        {
					id: 0,
					value: 0,
          type: "农业银行",
					card_num: 12312315,
					checked: true
        },
        {
					id: 1,
					value: 1,
          type: "邮政储蓄",
					card_num: 564864656,
					checked: false
        },
        {
					id: 2,
					value: 2,
          type: "农业银行",
					card_num: 897988989,
					checked: false
        }
      ]
    };
  },
  methods: {
		// 点击删除
    del(index) {
      uni.showModal({
        content: "确定删除吗?",
        success: res => {
          if (res.confirm) {
            this.list.splice(index, 1);
          } else if (res.cancel) {
            console.log("用户点击取消");
          }
        }
      });
		},
		// 去绑定银行卡页面
		goWalletBindingCard() {
			uni.navigateTo({
        url: '/pages/walletBindingCard/walletBindingCard'
      })
		},
		checkboxChange(e) {
		for (let i = 0; i < this.list.length; i++) {
			this.list[i].checked = false			
		}
		console.log(e.detail.value)
		this.list[e.detail.value].checked = true
		}
  }
};
